.Navbar{
  width: 100%;
  height: 72px;

  display: flex;
  position: fixed;
  z-index: 5;
  background-color:#F4F3E7;

  font-family: 'BPG Mrgvlovani';
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
}

.logo{
    position: absolute;
    left: 48px;
    top: -2px;
    cursor: pointer;
}

.nav_menu{
    position: fixed;
    margin-left: 200px;
    top: 35px;
    display: flex;
    gap: 60px; 
}

li{
    position: relative;
    list-style: none;
    cursor: pointer;
}

li::after{
    content: '';
    position: absolute;
    width: 0%;
    height: 2px; 
    left: 0;
    background-color: black;
    bottom: -3px;
    transition: all 0.4s ease;
}

li:hover:after{
    width: 100%;
}

.languages{
    position: absolute;
    right: 85px;
    top: 35px;
    display: flex;
    gap: 2px;
}

.active_page{
    position: relative;
}

.languages span{
    cursor: pointer;
}

.active_language{
    color: #029FA0;
}

.active_page::before{
    content: '';
    position: absolute;
    bottom: -3px;
    left:0;
    width: 100%;
    height: 2px;
    background-color: #01BCA7;
}


