.why_panorama{
    position: relative;
    width: 100%;
    height: 700px;
    background-color: #A9C1A9;
    padding-top: 0.1px;
    padding-left: 70px;
    padding-right: 120px;
    box-sizing: border-box;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 170px;
}

.header_txt_btn{
    margin-top: 60px;
}

.why_panorama_h1{
    font-family: 'BPG Arial';
    font-style: normal;
    font-weight: 400;
    font-size: 50px;
    line-height: 57px;
    color: #171F17;
}

.why_panorama_txt{
    margin-top: 42px;
    width: 629px;
    font-family: 'BPG Mrgvlovani';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 31px;
    /* or 155% */

    display: flex;
    align-items: center;

    color: #171F17;
}

.why_panorama_btn{
    margin-top: 42px;
    width: 233.18px;
    height: 53.3px;

    background: #029FA0;
    border-radius: 20px;

    display: flex;
    align-items: center;
    justify-content: center;
}

.why_panorama_btn span{
    font-family: 'bpg web 001';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;

    color: #F4F3E7;
}

.float_cliff{
    height: 600px;
    width: auto;
}